.header {
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding-top: 20px;
  background-color: white; // Чёрный фон
  width: 90%;
  margin: auto;

  @media screen and (max-width: 480px) {
    width: 95%;
  }
}

.logoContainer {
  display: flex;
  align-items: center;
}

.logo {
  width: 50px;
  height: auto;

  @media screen and (max-width: 480px) {
    width: 40px;
  }
}

.nav {
  display: flex;
  align-items: center;
  gap: 20px;
}

.partnerButton {
  background-color: #007bff; // Синяя кнопка
  color: #fff;
  border: none;
  padding: 10px 15px;
  border-radius: 5px;
  cursor: pointer;
  font-weight: bold;
}

.partnerButton:hover {
  background-color: #0056b3;
}

.navItem {
  color: black;
  cursor: pointer;
  font-size: 16px;

  @media screen and (max-width: 480px) {
    font-size: 12px;
  }
}

.navItem:hover {
  text-decoration: underline;
}

.button {
  position: relative;
  padding: 10px 20px;
  width: fit-content;
  font-size: 16px;
  font-weight: 500;
  color: white;
  background-color: #0171FF; 
  border: none;
  cursor: pointer;
  overflow: hidden;
  border-radius: 10px;
  transition: all 0.4s;


   &:hover {
    background-color: #0044cc;
  }

  @media (max-width: 480px) { 
    width: fit-content;
    height: 40px;
    font-size: 14px;
  }
  
  

}
