

.wrapper {
  padding: 80px 0;
}

.container {
  max-width: 1200px;
  margin: 0 auto;
  padding: 0 20px;

  h2 {
    font-size: 48px;
    font-weight: 500;

    @media screen and (max-width: 768px) {
      font-size: 28px;
    }
  }
}



.blocks {
  display: flex;
  flex-wrap: wrap;
  gap: 20px;
  list-style: none;
  padding: 0;
  margin-top: 40px;

  @media screen and (max-width: 768px) {
    margin-top: 20px;
  }
}

.blocks li {
  flex: 1 1 calc(33.333% - 13.33px);
  background-color: #F8FBFF;
  border: 1px solid rgba(1, 113, 255, 0.2);
  border-radius: 16px;
  padding: 24px;
  box-sizing: border-box;
  transition: box-shadow 0.2s ease;
}

.blocks li:nth-child(-n + 3) {
  /* высота по самому высокому элементу */
  height: 279px;

  @media screen and (max-width: 768px) {
    height: unset;
    min-height: 150px;
  }
  
}

.blocks li:hover {
  box-shadow: 0 4px 20px rgba(0, 0, 0, 0.05);
}

.blocks li:nth-child(4) {
  flex: 1 1 100%;
  display: flex;
  height: 176px;
  flex-direction: column;
  position: relative;

  @media screen and (max-width: 768px) {
    height: 250px;
  }
}

.blocks li h3 {
  margin-top: 0;
  color: #2563eb;
  font-size: 20px;
  margin-bottom: 30px;
  @media screen and (max-width: 768px) {
    margin-bottom: 20px;
  }
}

.blocks li p {
  margin-bottom: 16px;
  font-size: 16px;
  color: #1f2937;

  @media screen and (max-width: 768px) {
    font-size: 14px;
  }
}

.button {

}

.buttonWrapper {
    position: absolute;
    bottom: 24px;
    right: 24px;
  }

@media (max-width: 768px) {
  .blocks li {
    flex: 1 1 100%;
  }
}
