

.container {
    width: 80%;
    margin: auto;

    .blocks {
        display: flex;
        flex-direction: column;
        gap: 40px

        
    }

    .row {
            display: flex;
            justify-content: space-between;
        }


    @media screen and (max-width: 614px) {
        .row {
            display: flex;
            flex-direction: column;
            gap: 40px
        }
    }    
        
}