.container {
  background-color: #f9fbfe;
  padding: 40px 20px;
  border-radius: 24px;
}

.content {
  max-width: 1200px;
  margin: 0 auto;
  display: flex;
  flex-direction: column;
  gap: 40px;
}

.titleWrapper {
  display: flex;
  align-items: center;
  justify-content: space-between;
  flex-wrap: wrap;

  .title {
    font-size: 24px;
    font-weight: 500;
    margin: 0;
  }

  .logo {
    margin-top: 12px;
  }
}

.info {
  display: flex;
  justify-content: space-between;
  gap: 40px;
  flex-wrap: wrap;
}

.navList {
  list-style: none;
  padding: 0;
  margin: 0;

  li {
    margin-bottom: 12px;

    a {
      text-decoration: none;
      color: #2563eb;
      font-size: 20px;

      &:hover {
        text-decoration: underline;
      }
    }
  }
}

.contacts {
  display: flex;
  flex-direction: column-reverse;
  gap: 12px;
  font-style: normal;

  a {
    color: #2563eb;
    font-size: 16px;
    text-decoration: none;

    &:hover {
      text-decoration: underline;
    }
  }
}

@media (max-width: 768px) {
  .titleWrapper {
    flex-direction: column;
    align-items: flex-start;
    gap: 12px;
  }

  .info {
    flex-direction: column;
    gap: 24px;
  }
}


.logo {
  width: 50px;
  height: auto;

  @media screen and (max-width: 480px) {
    width: 40px;
  }
}
