@use "../../config-scss.scss" as *;

.container {
  background: white;
  border-radius: 20px;
  max-width: 1200px;
  margin: auto;
  display: flex;
  flex-direction: column;
  padding: 60px 20px;
  
  @media (max-width: 1024px) {
    margin: 0 40px;
  }

  @media (max-width: 768px) {
    margin: 0 20px;
    padding: 40px 15px;
  }
}

.title {
  font-size: 48px;
  font-weight: 500;
  margin-bottom: 71px;
  text-align: center;
  
  @media screen and (max-width: 480px) {
    font-size: 32px;
    margin-bottom: 40px;
  }
}

.grid {
  display: flex;
  flex-direction: column;
  gap: 71px;
}

.row {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  gap: 71px;
  
  @media (max-width: 768px) {
    grid-template-columns: 1fr;
    gap: 40px;
  }
}

.step {
  position: relative;
}

.fullWidthStep {
  position: relative;
  grid-column: 1 / -1;
  
  .description {
    max-width: 800px;

  }
}

.number {
  position: absolute;
  top: -30px;
  left: 0;
  font-size: 48px;
  color: rgba(0, 0, 0, 0.1);
  
  @media (max-width: 768px) {
    top: -25px;
    font-size: 36px;
  }
}

.stepTitle {
  font-size: 24px;
  font-weight: 500;
  color: #0171FF;
  margin-bottom: 10px;

  @media screen and (max-width: 768px) {
    font-size: 20px;
  }
}

.description {
  font-size: 16px;
  color: #333;
  
  @media screen and (max-width: 480px) {
    font-size: 14px;
  }
}

.button {
  margin-top: 80px;
  align-self: center;

  &:hover {
    background-color: #0044cc;
  }

  @media screen and (max-width: 768px) {
    margin-top: 40px;
  }
}