

.wrapper {
    width: 100%;
    max-width: 1200px;
    margin: auto;
}

.container {
    width: 100%;
    margin: auto;
    background: #F8FBFF;
    display: flex;
    justify-content: space-between;
    align-items: center;
    gap: 35px;
    padding: 0 53px;
    border-radius: 30px;

    @media screen and (max-width: 480px) {
        padding: 20px 20px;
    }
}


.subtitle {
    font-size: 32px;
    margin-bottom: 41px;

    @media screen and (max-width: 480px) {
        margin-bottom: 20px;
        font-size: 28px;
    }

}


.options {
    list-style: none;
    padding-left: 0;
    margin: 0;
    display: flex;
    flex-direction: column;
    gap: 15px;

    li  {
        display: flex;
        align-items: center;
        gap: 9px;
        font-size: 16px;


@media screen and (max-width: 480px) {
        font-size: 14px;
    }
        


        span {
            font-size: 36px;
            color: #DDECFF;
            width: 45px;
            display: block;
            font-weight: 500;

        }
    }
}

@media (max-width: 768px) {
    .imageWrapper {
        display: none;  // на мобильных устройствах скрываем картинку
    }
}