@use  '../../config-scss.scss' as *;

.container {
    width: 100%;
    margin: auto;
    border-radius: 10px;
    border: 1px solid #0171FF;

    .block {
        height: 60px;
        width: 100%;
        display: flex;
        align-items: center;
        justify-content: space-between;
        padding: 20px;
        cursor: pointer;
    }

    .text {
        color: white;
        font-size: 20px;
        font-weight: 400;
        padding-bottom: 20px;
    }

    .title {
        font-size: 24px;
        color: white;
        font-weight: 400;

        @media screen and (max-width: 480px) {
    font-size: 14px;
  }
    }


    .content {
    max-height: 0;
    opacity: 0;
    overflow: hidden;
    padding: 0 20px;
    margin: auto;
    transition: max-height 0.4s ease, opacity 0.3s ease;
}

.content.active {
    display: block;
    opacity: 1;
    max-height: 1000px; // Увеличиваем, чтобы контент не обрезался
}

    @media screen and (max-width: 1200px) {
        width: 100%;

        .title {
            font-size: 20px;
        }

        .text {
            font-size: 18px;
        }
    }

    @media screen and (max-width: 480px) {
        width: 100%;

        .title {
            font-size: 16px;
        }

        .text {
            font-size: 14px;
        }

    }


}


@keyframes textOpacity {
    0% {
        display: block;
    }

    100% {
        display: none;
    }
}
