@use '../../config-scss.scss' as *;

.container {
  width: 100%;
  position: relative;
  margin: auto;
  padding: 14px 0;
  overflow: hidden; // Предотвращает появление горизонтального скролла на странице

  @media screen and (max-width:768px) {
    height: 60px;
    padding: 10px 0;
  }

  &::before, 
  &::after {
    content: '';
    position: absolute;
    width: 0%;
    height: 1px;
    background-color: white;
  }

  &::before {
    top: 0;
    right: 0;
    animation: drawBorderRight 1.5s ease-out forwards;
  }

  &::after {
    bottom: 0;
    left: 0;
    animation: drawBorderLeft 1.5s ease-out forwards;
  }
}

.icons {
  display: flex;
  justify-content: space-between;
  width: 90%;
  margin: auto;
  gap: 2rem;


  
  @media screen and (max-width: 768px) {
    width: 100%;
    overflow-x: auto; // Добавляем горизонтальный скролл
    scroll-snap-type: x mandatory;
    -webkit-overflow-scrolling: touch; // Улучшает скроллинг на iOS
    padding-bottom: 10px; // Чуть отступ снизу, чтобы контент не обрезался
    white-space: nowrap; // Элементы в строку без переноса
    padding-left: 2rem; // Отступ слева
    padding-right: 2rem; // Отступ слева

    &::-webkit-scrollbar {
      display: none; // Скрываем скроллбар
    }
  }
}

.text {
  font-size: 20px;
  color: #B1B1B1;
  white-space: nowrap;
  opacity: 0;
  transform: translateX(-20px);
  animation: fadeInText 1s ease-out forwards;
  
  @for $i from 1 through 10 {
    &:nth-child(#{$i}) {
      animation-delay: #{$i * 0.2}s;
    }
  }

  @media screen and (max-width: 1200px) {
    font-size: 18px;
  }

  @media screen and (max-width: 768px) {
    font-size: 16px;
  }

  @media screen and (max-width: 480px) {
    font-size: 14px;
  }
}

// Анимации

@keyframes fadeInText {
  from {
    opacity: 0;
    transform: translateX(-20px);
  }
  to {
    opacity: 1;
    transform: translateX(0);
  }
}

@keyframes drawBorderRight {
  from {
    width: 0%;
  }
  to {
    width: 100%;
  }
}

@keyframes drawBorderLeft {
  from {
    width: 0%;
  }
  to {
    width: 100%;
  }
}
