@use  '../../config-scss.scss' as *;

.container {
  display: flex;
  align-items: center;
  gap: 10px;
  overflow: hidden;
  margin-bottom: 30px;

  &.visible {
    .text {
      opacity: 1;
      animation: fadeInText 1s ease-out forwards;
    }

    .line {
      animation: drawLine 1s ease-out 0.3s forwards;
    }
  }
}

.text {
  font-size: 40px;
  font-weight: bold;
  color: $primary;
  opacity: 0;
}

.line {
  flex-grow: 1;
  height: 2px;
  background-color: $primary;
  transform: scaleX(0); 
  transform-origin: left;
}


@media screen and (max-width: 768px) {
  .text {
    font-size: 24px;
  }
}


@keyframes fadeInText {
  to {
    opacity: 1;
    transform: translateX(0);
  }
}

@keyframes drawLine {
  to {
    transform: scaleX(1);
  }
}
