@use  '../../config-scss.scss' as *;

.container {
    
    width: 200px;
    background-color: $quaternary;
    box-shadow: 10px 10px 0 rgb(186,196,216);
    padding: 10px;
    height: 110px;
    display: flex;
    justify-content: center;
    align-items: center;

    .text {
        font-size: $base;
        color: $primary;
        font-family: $text-font;
        line-height: 30px;
        font-weight: 600;
        text-align: center;
    }


    @media screen and (max-width: 1200px) {
        width: 180px;

        .text {
            font-size: 18px;
        }
    }
}



