

.container {
    width: 90%;
    margin: auto;

    .content {
        display: flex;
        flex-direction: column;
        gap: 20px;
        margin-top: 40px;
    }

    .title {
        color: white;
        font-size: 48px;
        font-weight: 500px;

        @media screen and (max-width: 480px) {
            font-size: 32px;
     }
    }

    @media screen and (max-width: 768px) {
        width: 90%;
    }
}