@use  '../../config-scss.scss' as *;

.button {
  position: relative;
  padding: 10px 20px;
  width: fit-content;
  font-size: 16px;
  font-weight: 500;
  color: white;
  background-color: #0171FF; 
  border: none;
  cursor: pointer;
  overflow: hidden;
  border-radius: 10px;
  transition: all 0.4s;


   &:hover {
    background-color: #0044cc;
  }

  @media (max-width: 480px) { 
    width: fit-content;
    height: 40px;
    font-size: 14px;
  }
  
  

}
