.wrapper {
  position: relative;
  overflow: hidden;
  text-align: center;
}

.imageWrapper {
  display: flex;
  justify-content: center;
  z-index: 1;
  position: relative;
  max-width: 1200px;
  margin: auto;
  padding: 0 80px;

  @media (max-width: 1024px) {
    padding: 0 40px;
  }

  @media (max-width: 768px) {
    padding: 0 20px;
    flex-direction: column;
  }
}

.backgroundSvg {
  width: 100%;
  max-width: 551px;
  height: auto;
  display: block;
}

.glowingCircle {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  width: 200px;
  height: 200px;
  border-radius: 50%;
  background: rgba(1, 113, 255, 1);
  filter: blur(100px);
  box-shadow: 0 0 100px 100px rgba(1, 113, 255, 0.5);
  z-index: -1;

  @media (max-width: 768px) {
    width: 150px;
    height: 150px;
    filter: blur(60px);
    box-shadow: 0 0 60px 60px rgba(1, 113, 255, 0.5);
  }
}

.blurOverlay {
  position: absolute;
  bottom: 0;
  left: 0;
  width: 100%;
  padding: 30px 80px;
  background: linear-gradient(180deg, rgba(243, 243, 243, 0.5) 0%, #ffffff 67.44%);
  backdrop-filter: blur(24px);
  -webkit-backdrop-filter: blur(24px);
  z-index: 2;
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 24px;
  border-top: 1px solid rgba(1, 113, 255, 0.3);

  @media (max-width: 1024px) {
    padding: 24px 40px;
  }

  @media (max-width: 768px) {
    padding: 20px 20px;
  }
}

.content {
  z-index: 3;
  color: #000;
  max-width: 800px;

  h1 {
    font-size: 64px;
    font-weight: bold;
    margin-bottom: 12px;

    @media (max-width: 1024px) {
      font-size: 48px;
    }

    @media (max-width: 768px) {
      font-size: 36px;
    }
  }

  h2 {
    font-size: 16px;

    @media (max-width: 768px) {
      font-size: 14px;
    }
  }

  p {
    font-size: 16px;
    color: black;
    margin-bottom: 20px;

    @media (max-width: 768px) {
      font-size: 14px;
    }
  }
}

.textServers {
  position: absolute;
  top: 70px;
  right: 20px;
  max-width: 500px;
  text-align: left;
  background-color: #ffffff;
  border-radius: 90px;
  padding: 20px;
  box-shadow: 0 0 15px 5px rgba(1, 113, 255, 0.1);

  @media (max-width: 768px) {
    position: static;
    margin: 20px auto 10px;
    text-align: center;
    font-size: 12px;
  }
}

.textInfra {
  position: absolute;
  top: 140px;
  left: 20px;
  max-width: 500px;
  text-align: left;
  background-color: #ffffff;
  border-radius: 90px;
  padding: 20px;
  box-shadow: 0 0 15px 5px rgba(1, 113, 255, 0.1);


  @media screen and (max-width: 1024px) {
    top: 180px;
  }

  @media (max-width: 768px) {
    position: static;
    margin: 10px auto 20px;
    text-align: center;
    font-size: 12px;
  }
}
