@use '../../config-scss.scss' as *;

.iconTitle {
  display: flex;
  align-items: center;
  border-radius: 1.2rem;
  border: 1px solid white;
  padding: 1rem 2.5rem;
  transition: all 0.3s ease-in-out; 

  .icon {
    width: 2rem;
    height: 2rem;
    margin-right: 0.8rem;
    opacity: 0;
    transform: translateX(-10px);
    animation: fadeIn 0.5s ease-out forwards;
  }

  .text {
    font-size: 20px;
    color: white;
    opacity: 0;
    transform: translateX(-10px);
    animation: fadeIn 0.5s ease-out forwards;
    animation-delay: 0.2s;
  }

  @media (max-width: 1024px) {
    padding: 0.8rem 2rem;


    .icon {
      width: 1.8rem;
      height: 1.8rem;
    }
  }

  @media (max-width: 768px) {
    padding: 0.7rem 1.8rem;

    .icon {
      width: 1.6rem;
      height: 1.6rem;
    }

    .text {
      font-size: 14px;
    }
  }

  @media (max-width: 480px) {
    padding: 0.6rem 1.5rem;

    .text {
      font-size: 14px;
    }

    .icon {
      width: 1.4rem;
      height: 1.4rem;
    }
  }
}

@keyframes fadeIn {
  to {
    opacity: 1;
    transform: translateX(0);
  }
}
