
@use  '../../config-scss.scss' as *;

.containerBig {
  width: 55%;
  background-color: $secondary;
  box-shadow: 10px 10px 0 rgb(186,196,216);
  padding: 10px;
  height: 170px;

  .text {
    font-size: $base;
    color: $quaternary;
    font-family: $text-font;
    line-height: 30px;
    font-weight: 500;
  }

  @media screen and (max-width:768px) {
    .text {
      font-size: 16px;
    }
}

}

.containerSmall {
  width: 40%;
  background-color: $secondary;
  box-shadow: 10px 10px 0 rgb(186,196,216);
  padding: 10px;
  height: 170px;

  .text {
    font-size: $base;
    color: $quaternary;
    font-family: $text-font;
    line-height: 30px;
    font-weight: 500;
  }

   @media screen and (max-width:768px) {
    .text {
      font-size: 16px;
    }
}
}


@media (max-width: 614px) {
  .containerBig,
  .containerSmall {
    width: 100%; 
  }
}
