.container {
    display: flex;
    align-items: center;
    justify-content: center;
    position: relative;
    overflow: hidden;
    width: 100%;
    gap: 10px;
    opacity: 0; 
    transform: translateY(20px); 
    transition: opacity 0.6s ease, transform 0.6s ease; 
  }
  
  .container.visible {
    opacity: 1; 
    transform: translateY(0); 
  }
  
  .text {
    font-size: 40px;
    font-weight: bold;
    color: #002366;
    white-space: nowrap;
    opacity: 0; 
    animation: fadeInText 0.6s ease forwards;
    animation-delay: 0.3s; 

    @media screen and (max-width: 768px) {
      font-size: 24px;
    }
  }
  
  .lineLeft,
  .lineRight {
    height: 2px;
    background-color: #002366;
    transform: translateY(-50%);
    animation: drawLine 0.6s ease forwards;
  }
  
  .lineLeft {
    left: 0;
    animation-delay: 0.3s; 
  }
  
  .lineRight {
    right: 0;
    transform-origin: right;
    animation-delay: 0.3s;
  }
  

  @keyframes fadeInText {
    to {
      opacity: 1;
    }
  }
  
  @keyframes drawLine {
    to {
      width: 50%;
    }
  }
  