@use '../../config-scss.scss' as *;

.containerBig {
  padding: 20px;
  display: flex;
  justify-content: space-between;
  border-radius: 10px;
  border: 1px solid #7F7F7F;
  gap: 10px;
  align-items: center; // добавлено для выравнивания по центру
  width: 70%;

  @media screen and (max-width: 1200px) {
    width: 80%;
  }

  @media screen and (max-width: 468px) {
    padding: 10px;
    width: 100%;
  }

  @media screen and (max-width: 988px) {
    width: 100%;
  }

  // @media screen and (min-width: ) {
    
  // }

  .image {
    min-height: 300px;
    object-fit: cover; // чтобы изображение не искажалось

  }

  .title {
    color: white;
    margin-top: 20px;
    margin-bottom: 40px;
    font-size: clamp(20px, 2vw, 32px);
    font-weight: 400;
    width: 100%;
    max-width: 90%;
  }

  .options {
    margin-top: 10px;
    width: 100%;
    max-width: 90%;
  }

  .option {
    display: flex;
    margin-bottom: 10px;
    align-items: start;
    font-size: clamp(14px, 2vw, 32px);
    color: white;
  }

  li::before {
    content: "•";
    float: left;
    padding: 0 0.5em 0 0;
  }

  @media screen and (max-width: 1200px) {


    .image {
      min-height: unset;
      height: 250px; // Уменьшаем размер изображения для экранов до 1200px
    }
  }

  @media screen and (max-width: 768px) {


    .image {
      height: 200px; // Уменьшаем размер изображения для экранов до 768px
    }
  }

  @media screen and (max-width: 468px) {

    
    .title {
    
      margin-bottom: 10px;
      margin: 0;
    }

    .option {
    }

    .image {
      display: none;
      height: 120px; // Уменьшаем размер изображения для самых маленьких экранов
    }
  }
}
